.blur {
  background: url("../../images/bobRossMountain.jpg") no-repeat center center fixed;
  background-size: cover;
  overflow: hidden;
  -webkit-filter: blur(13px);
          filter: blur(13px);
  position: absolute;
  height: 410px;
  top: -50px;
  left: -50px;
  right: -50px;
  bottom: -50px;
}

.widget {
  border-top: 2px solid rgba(255, 255, 255, .5);
  border-bottom: 2px solid rgba(255, 255, 255, .5);
  height: 200px;
  width: 100%;
  overflow: hidden;
}

.center {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 350px;
}

#logo-image {
    left: 0;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    margin-left: 330px;
}





/*  NOT REQUIRED  */

.text {
  height: 200px;
  width: 340px;
}

.text h1 {
  text-align: center;
  text-shadow: 1px 1px rgba(0, 0, 0, .1);
  color: #ffffff;
  margin-top: 57px;
  font-weight: 700;
  font-size: 52px;
}

.text p {
  text-align: center;
  color: #ffffff;
  text-shadow: 1px 1px rgba(0, 0, 0, .1);
  margin-top: 0px;
  font-weight: 400;
  font-size: 22px;
}


